import React, {useState} from "react";
import styles from "./Burger.module.css";
import icon from "../../img/burger.png";
import close from "../../img/close_menu.png";
import {Link} from "react-scroll";

const Burger: React.FC = () => {

    const [editMenu, setEditMenu] = useState(false)

    const editMenuHandler = () => {
        setEditMenu(!editMenu)
    }

    const iconActive = `${styles.burger} ${editMenu && styles.burgerActive}`
    const menuActive = `${styles.menu} ${editMenu && styles.menuActive}`

    return (
        <>
            <div onClick={editMenuHandler} className={iconActive}>
                <div className={styles.burgerCircle}>
                    <img src={icon} alt="menu"/>
                </div>
            </div>

            <nav className={menuActive}>
                <ul className={styles.menuList}>

                    {/*Hero link*/}
                    <Link activeClass={styles.active}
                          className={styles.menuItem}
                          to="hero"
                          spy={true}
                          smooth={true}
                          duration={500}
                          offset={0}
                    >
                        Home
                    </Link>

                    {/*Skills link*/}
                    <Link activeClass={styles.active}
                          className={styles.menuItem}
                          to="skills"
                          spy={true}
                          smooth={true}
                          duration={500}
                          offset={0}
                    >
                        Skills
                    </Link>

                    {/*Projects link*/}
                    <Link activeClass={styles.active}
                          className={styles.menuItem}
                          to="projects"
                          spy={true}
                          smooth={true}
                          duration={500}
                          offset={0}
                    >
                        Projects
                    </Link>

                    {/*Contacts link*/}
                    <Link activeClass={styles.active}
                          className={styles.menuItem}
                          to="contacts"
                          spy={true}
                          smooth={true}
                          duration={500}
                          offset={0}
                    >
                        Contact
                    </Link>
                </ul>
                <img onClick={editMenuHandler} className={styles.close} src={close} alt="close menu"/>
            </nav>
        </>
    )
}

export default Burger;
